/* eslint-disable jsx-a11y/anchor-is-valid */
import React from "react";
import { Grid, Cell } from "react-mdl";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import "./index.css";
import { resetPassword } from "../../redux/actions/common_actions";

class FPassword extends React.Component {
  state = {
    email: "",
    error: false,
    message: "",
    link: false,
    try: false
  };
  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
    this.setState({ error: false });
  };

  handleSubmit = e => {
    e.preventDefault();
    if (this.state.email === "") {
      return this.setState({
        error: true
      });
    }
    this.setState({ error: false });

    //call the database here
    this.props.dispatch(
      resetPassword(this.state.email, (data, err) => {
        if (err) {
          this.setState({
            message: err.message,
            link: true
          });
        } else {
          this.setState({ message: data.message, try: true });
        }
      })
    );
  };
  render() {
    return (
      <div className="reg__body___editor__login registration_form">
        <Grid>
          <Cell col={4} />
          <Cell col={4}>
            <header id="main-header">
              <div className="main-logo">
                <Link to="/">
                  <h3 style={{ color: "black", textAlign: 'center' }}>
                    <img src="/img/logo.svg" alt="logo" />
                  </h3>
                </Link>
                {/* {JSON.stringify(this.state)} */}
              </div>
            </header>
            {this.state.message ? (
              <form className="spread">
                <p>{this.state.message}</p>
                {this.state.try ? (
                  <a href="/resetpassword" style={{ color: "rgb(1, 43, 111)" }}>
                    Try Again
                  </a>
                ) : null}
                {this.state.link ? (
                  <Link to="/" style={{ color: "rgb(0, 0, 255)" }}>
                    Back to Home Page
                  </Link>
                ) : null}
              </form>
            ) : (
                <form
                  id="user_form"
                  className="spread"
                  acceptCharset="UTF-8"
                  _lpchecked="1"
                  onSubmit={e => this.handleSubmit(e)}
                >
                  <div className="row">
                    <p>
                      Let us give your registered email address to RESET your
                      password.
                  </p>
                    <label className="above" htmlFor="registration_form_email">
                      Email
                  </label>
                    <input
                      id="email"
                      type="email"
                      autoFocus="autofocus"
                      value={this.state.email}
                      onChange={this.handleChange("email")}
                      placeholder={
                        this.state.error ? "This should be a valid email" : null
                      }
                    />
                  </div>

                  <div className="buttons with-side-link">


                    {this.state.email === "" ? (
                      <a
                        class="waves-effect waves-light btn-large bg-primary rounded"
                        onClick={e => this.handleSubmit(e)}
                      >Reset Password</a>
                    ) : (
                        <a class="waves-effect waves-light btn-large bg-primary rounded disabled">Reset Password</a>
                      )}

                  </div>
                </form>
              )}
          </Cell>
          <Cell col={4} />
        </Grid>
        <footer id="devise-footer">
          <ul>
            <li>
              <a href="/terms_of_use">Terms of use</a>
            </li>
            <li>
              <a href="/impressum">Imprint</a>
            </li>
            <li>
              <a href="/privacy">Privacy</a>
            </li>
          </ul>
          <p className="copyright">© 2019 Company Name</p>
        </footer>
      </div>
    );
  }
}

export default connect()(FPassword);
