import {
  SUBMIT_COMMON_INFO,
  CONTINUE_WITH_COMMON_INFO
} from "../actions/types";

export default function(state = {}, action) {
  switch (action.type) {
    case SUBMIT_COMMON_INFO:
      return { ...state, homePageData: action.payload };
    case CONTINUE_WITH_COMMON_INFO:
      return { ...state, user: action.payload };
    default:
      return state;
  }
}
