import React from "react";
// import $ from "jquery";
// import jQuery from "jquery";
import "./Slider.css";

class Slider extends React.Component {
  state = {};
  componentDidMount() {
    var testim = document.getElementById("testim"),
      testimDots = Array.prototype.slice.call(
        document.getElementById("testim-dots").children
      ),
      testimContent = Array.prototype.slice.call(
        document.getElementById("testim-content").children
      ),
      testimLeftArrow = document.getElementById("left-arrow"),
      testimRightArrow = document.getElementById("right-arrow"),
      testimSpeed = 2000,
      currentSlide = 0,
      currentActive = 0,
      testimTimer,
      touchStartPos,
      touchEndPos,
      touchPosDiff,
      ignoreTouch = 30;
    window.onload = function() {
      // Testim Script
      function playSlide(slide) {
        for (var k = 0; k < testimDots.length; k++) {
          testimContent[k].classList.remove("active");
          testimContent[k].classList.remove("inactive");
          testimDots[k].classList.remove("active");
        }

        if (slide < 0) {
          slide = currentSlide = testimContent.length - 1;
        }

        if (slide > testimContent.length - 1) {
          slide = currentSlide = 0;
        }

        if (currentActive !== currentSlide) {
          testimContent[currentActive].classList.add("inactive");
        }
        testimContent[slide].classList.add("active");
        testimDots[slide].classList.add("active");

        currentActive = currentSlide;

        clearTimeout(testimTimer);
        testimTimer = setTimeout(function() {
          playSlide((currentSlide += 1));
        }, testimSpeed);
      }

      for (var l = 0; l < testimDots.length; l++) {
        // eslint-disable-next-line no-loop-func
        testimDots[l].addEventListener("click", function() {
          playSlide((currentSlide = testimDots.indexOf(this)));
        });
      }

      playSlide(currentSlide);

      testim.addEventListener("touchstart", function(e) {
        touchStartPos = e.changedTouches[0].clientX;
      });

      testim.addEventListener("touchend", function(e) {
        touchEndPos = e.changedTouches[0].clientX;

        touchPosDiff = touchStartPos - touchEndPos;
        if (touchPosDiff > 0 + ignoreTouch) {
          testimLeftArrow.click();
        } else if (touchPosDiff < 0 - ignoreTouch) {
          testimRightArrow.click();
        } else {
          return;
        }
      });
    };
  }
  render() {
    return (
      <section id="testim" className="testim bg-primary">
        <div className="div-title">
          <h3 style={{ paddingTop: "25px" }}>What people say about us?</h3>
        </div>
        <div className="testim-cover">
          <div className="wrap">
            <ul id="testim-dots" className="dots">
              <li className="dot active" />
              <li className="dot" />
              <li className="dot" />
              <li className="dot" />
              <li className="dot" />
            </ul>
            <div id="testim-content" className="cont">
              <div className="active">
                <div className="img">
                  <img
                    src="https://image.ibb.co/hgy1M7/5a6f718346a28820008b4611_750_562.jpg"
                    alt=""
                  />
                </div>
                <h2>Lorem P. Ipsum</h2>
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud exercitation ullamco.
                </p>
              </div>

              <div>
                <div className="img">
                  <img
                    src="https://image.ibb.co/cNP817/pexels_photo_220453.jpg"
                    alt=""
                  />
                </div>
                <h2>Mr. Lorem Ipsum</h2>
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud exercitation ullamco.
                </p>
              </div>

              <div>
                <div className="img">
                  <img
                    src="https://image.ibb.co/iN3qES/pexels_photo_324658.jpg"
                    alt=""
                  />
                </div>
                <h2>Lorem Ipsum</h2>
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud exercitation ullamco.
                </p>
              </div>

              <div>
                <div className="img">
                  <img
                    src="https://image.ibb.co/kL6AES/Top_SA_Nicky_Oppenheimer.jpg"
                    alt=""
                  />
                </div>
                <h2>Lorem De Ipsum</h2>
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud exercitation ullamco.
                </p>
              </div>

              <div>
                <div className="img">
                  <img src="https://image.ibb.co/gUPag7/image.jpg" alt="" />
                </div>
                <h2>Ms. Lorem R. Ipsum</h2>
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud exercitation ullamco.
                </p>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default Slider;
