import React from "react";
import $ from "jquery";
import { Grid, Cell } from "react-mdl";
import "./index.css";
import axios from "axios";

class Contact extends React.Component {
  state = {
    name: "",
    email: "",
    message: "",
    warning: {}
  };
  componentDidMount() {
    $(".js-input").keyup(function() {
      if ($(this).val()) {
        $(this).addClass("not-empty");
      } else {
        $(this).removeClass("not-empty");
      }
    });
  }

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
  };

  handleSubmit = async e => {
    e.preventDefault();
    if (
      this.state.email === "" ||
      this.state.message === "" ||
      this.state.message === ""
    ) {
      return this.setState({ warning: { faild: "Every Field Are Required" } });
    }
    await axios
      .post("https://emailserverclient.herokuapp.com/email", this.state)
      .then(({ data }) => {
        if (data.success) {
          this.setState({
            name: "",
            email: "",
            message: "",
            warning: {
              success:
                "Thanks for your message. We will reply you as soon as possible."
            }
          });
        }
      });
  };

  render() {
    return (
      <Grid>
        <Cell col={1} />
        <Cell col={10}>
          <section className="get-in-touch">
            {this.state.warning.success ? (
              <h4
                style={{
                  color: "green",
                  textAlign: "center",
                  fontFamily: "Lato"
                }}
              >
                {this.state.warning.success}
              </h4>
            ) : (
              <form
                className="contact-form row"
                onSubmit={e => this.handleSubmit(e)}
                action="mailto:forhadhossenbabu@gmail.com"
                method="post"
                encType="text/plain"
              >
                <div className="form-field col x-50">
                  <input
                    id="name"
                    className="input-text js-input"
                    type="text"
                    // required
                    placeholder={
                      this.state.error ? "this field required" : null
                    }
                    onChange={this.handleChange("name")}
                  />
                  <label className="label" htmlFor="name">
                    Name
                  </label>
                </div>
                <div className="form-field col x-50">
                  <input
                    id="email"
                    className="input-text js-input"
                    type="email"
                    // required
                    placeholder={
                      this.state.error ? "this field required" : null
                    }
                    onChange={this.handleChange("email")}
                  />
                  <label className="label" htmlFor="email">
                    E-mail
                  </label>
                </div>
                <div className="form-field col x-100">
                  <input
                    id="message"
                    className="input-text js-input"
                    type="text"
                    // required
                    placeholder={
                      this.state.error ? "this field required" : null
                    }
                    onChange={this.handleChange("message")}
                  />
                  <label className="label" htmlFor="message">
                    Message
                  </label>
                </div>
                {this.state.warning.faild ? (
                  <p style={{ color: "red", textAlign: "center" }}>
                    {this.state.warning.faild}
                  </p>
                ) : null}
                {/* {this.state.warning.success ? (
                <p style={{ color: "green", textAlign: "center" }}>
                  {this.state.warning.success}
                </p>
              ) : null} */}
                <div className="form-field col x-100 align-center">
                  <button className="btn btn-own color1 mr-10 btn-101">
                    Submit
                  </button>
                </div>
              </form>
            )}
          </section>
        </Cell>
        <Cell col={1} />
      </Grid>
    );
  }
}

export default Contact;
