import React from "react";
import { Grid, Cell } from "react-mdl";
import { Link, withRouter } from "react-router-dom";
import { connect } from "react-redux";
import "./index.css";
import { signInUser } from "../../redux/actions/common_actions";

class LogIn extends React.Component {
  state = {
    email: "",
    password: "",
    error: false
  };

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
    this.setState({ error: false });
  };
  handleClick = () => {
    this.setState({ error: false });
  };
  handleSubmit = e => {
    e.preventDefault();
    if (this.state.email === "" || this.state.password === "") {
      return this.setState({
        error: true
      });
    }
    if (this.state.password.length < 6) {
      this.setState({ error: true });
      return this.setState({ password: "" });
    }
    this.setState({ error: false });
    this.props
      .dispatch(signInUser(this.state.email, this.state.password))
      .then(res => {
        if (res.payload.loginStatus.loginSuccess) {
          this.props.history.push("/dashbord");
        }
        if (!res.payload.loginStatus.loginSuccess) {
          console.log("Redirected to False");
        }
      });
  };
  renderError = () => {
    if (this.props.user) {
      if (this.props.user.loginStatus.loginSuccess === true) {
        if (!this.props.user.loginStatus.user.user.emailVerified) {
          return (
            <p style={{ color: "red" }}>
              Email Not Verified. Please Verify Your Email.
            </p>
          );
        }
      }
      if (this.props.user.loginStatus.loginSuccess === false) {
        return (
          <p style={{ color: "red" }}>
            {this.props.user.loginStatus.err.message}
          </p>
        );
      }
    }
  };
  render() {
    return (
      <div className="reg__body___editor__login registration_form">
        <Grid>
          <Cell col={4} />
          <Cell col={4}>
            <header id="main-header">
              <div className="main-logo">
                <Link to="/">
                  <h3 style={{ color: "black", textAlign: 'center' }}>
                    <img src="/img/logo.svg" alt="logo" />
                  </h3>
                </Link>
              </div>
            </header>
            <form
              id="user_form"
              className="spread"
              acceptCharset="UTF-8"
              _lpchecked="1"
              onSubmit={e => this.handleSubmit(e)}
            >
              <div className="row">
                <p>It's a simple message to our all user.</p>
                <label className="above" htmlFor="registration_form_email">
                  Email
                </label>
                <input
                  id="email"
                  type="email"
                  autoFocus="autofocus"
                  value={this.state.email}
                  onChange={this.handleChange("email")}
                  onClick={this.handleClick}
                  placeholder={
                    this.state.error ? "This should be a valid email" : null
                  }
                />
              </div>
              <div className="row">
                <label className="above" htmlFor="registration_form_password">
                  Password
                </label>
                <input
                  type="password"
                  id="registration_form_password"
                  aria-autocomplete="list"
                  value={this.state.password}
                  onChange={this.handleChange("password")}
                  onClick={this.handleClick}
                  placeholder={
                    this.state.error
                      ? "Password length must be 6 characters"
                      : null
                  }
                />
                <p style={{ color: "red", marginTop: "15px" }}>
                  {this.state.errorMsg ? this.state.errorMsg : null}
                </p>
              </div>
              <div>{this.renderError()}</div>

              <div className=" with-side-link">              
                {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
                <a
                  class="waves-effect waves-light btn-large bg-primary rounded"
                  onClick={e => this.handleSubmit(e)}
                >Login</a>
              </div>
              <div className="buttons with-side-link">
                <small>
                  Don't have an account?{" "}
                  <Link to="/register" style={{ color: "blue !important" }}>
                    Register Here
                  </Link>
                </small>
                <small>
                  <Link
                    to="/resetpassword"
                    style={{ color: "blue !important" }}
                  >
                    Forget Password
                  </Link>
                </small>
                </div>
            </form>
          </Cell>
          <Cell col={4} />
        </Grid>
        <footer id="devise-footer">
          <ul>
            <li>
              <Link to="/">
                <i className="fas fa-long-arrow-alt-left" /> Back To Home Page
              </Link>
            </li>
            <li>
              <Link to="/faq">FAQ</Link>
            </li>
            <li>
              <Link to="/contact">Contact Us</Link>
            </li>
            <li>
              <Link to="/privacy">Privacy Policy</Link>
            </li>
            <h6 style={{ color: "white" }}>© 2019 Company Name</h6>
          </ul>
        </footer>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    user: state.auth.user
  };
};

export default connect(mapStateToProps)(withRouter(LogIn));
