import React from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
  submitDataFromHomePage,
  SubmitValidateData
} from "../../../redux/actions/common_actions";
import "./Form.css";
import { AppBar, Tabs, Tab } from "@material-ui/core";

class MiniForm extends React.Component {
  state = { 
    name: "", 
    email: "", 
    error: false, 
    emailExits: false,
    accountType: "investor"
  };
  componentDidMount() {
    const placeholders = document.querySelectorAll(
        ".styled-input__placeholder-text"
      ),
      inputs = document.querySelectorAll(".styled-input__input");

    placeholders.forEach(function(el, i) {
      let value = el.innerText,
        html = "";
      for (let w of value) {
        if (!value) value = "&nbsp;";
        html += `<span class="letter">${w}</span>`;
      }
      el.innerHTML = html;
    });

    inputs.forEach(function(el) {
      let parent = el.parentNode;
      el.addEventListener(
        "focus",
        function() {
          parent.classList.add("filled");
          placeholderAnimationIn(parent, true);
        },
        false
      );
      el.addEventListener(
        "blur",
        function() {
          if (el.value.length) return;
          parent.classList.remove("filled");
          placeholderAnimationIn(parent, false);
        },
        false
      );
    });

    function placeholderAnimationIn(parent, action) {
      let act = action ? "add" : "remove";
      let letters = parent.querySelectorAll(".letter");
      letters = [].slice.call(letters, 0);
      if (!action) letters = letters.reverse();
      letters.forEach(function(el, i) {
        setTimeout(function() {
          let contains = parent.classList.contains("filled");
          if ((action && !contains) || (!action && contains)) return;
          el.classList[act]("active");
        }, 20 * i);
      });
    }

    setTimeout(function() {
      document.body.classList.add("on-start");
    }, 30);

    setTimeout(function() {
      document.body.classList.add("document-loaded");
    }, 100);
  }

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value
    });
  };

  handleSubmit = async e => {
    e.preventDefault();
    console.log("button");
    if (this.state.email === "" || this.state.name === "") {
      return this.setState({ error: true });
    }
    if (this.state.name !== "" || this.state.email !== "") {
      this.setState({ error: false });
      const data = {
        name: this.state.name,
        email: this.state.email,
        accountType: this.state.accountType
      };
      try {
        let info = await this.props.submitDataFromHomePage(data);
        console.log(info);
        await this.props.SubmitValidateData(data);
        this.props.history.push("/register");
      } catch (e) {
        return this.setState({ emailExits: true });
      }
    }
  };
  handleClick = e => {
    this.setState({ error: false });
  };

  handleChangeTab = (event, accountType) => {
    this.setState({ accountType });
  };

  render() {
    return (
      <React.Fragment>
        <div id="registration-tab-home">
          <AppBar position="static">
            <Tabs
              value={this.state.accountType}
              onChange={this.handleChangeTab}
              indicatorColor="green"
            >
              <Tab label="Investor" value="investor" />
              <Tab label="Borrower" value="borrower" />
            </Tabs>
          </AppBar>
        </div>
        <form id="homepage-form" className="form" onSubmit={e => this.handleSubmit(e)}>

          <div className="form__content">
            <div className="styled-input">
              <input
                type="text"
                className="styled-input__input"
                style={{ width: "100%" }}
                value={this.state.name}
                onChange={this.handleChange("name")}
                onClick={this.handleClick}
              />
              <div className="styled-input__placeholder">
                <span className="styled-input__placeholder-text">
                  Full Name
                </span>
              </div>
              <div className="styled-input__circle" />
            </div>
            <div className="styled-input">
              <input
                type="email"
                className="styled-input__input"
                style={{ width: "100%" }}
                value={this.state.email}
                onChange={this.handleChange("email")}
                onClick={this.handleClick}
              />
              <div className="styled-input__placeholder">
                <span className="styled-input__placeholder-text">Email</span>
              </div>
              <div className="styled-input__circle" />
            </div>
            <div className="miniformbtn">
              {this.state.emailExits ? (
                <h5
                  style={{
                    color: "rgb(255, 71, 0)",
                    borderBottom: "2px solid rgba(255, 0, 0, 0.35)",
                    lineHeight: "1.5"
                  }}
                >
                  This email is used by another account
                </h5>
              ) : null}
              {this.state.error ? (
                <h5
                  style={{
                    color: "rgb(255, 71, 0)",
                    borderBottom: "2px solid rgba(255, 0, 0, 0.35)",
                    lineHeight: "1.5"
                  }}
                >
                  Fill The Input First
                </h5>
              ) : (
                <button
                  className="btn btn-own color1 mt-40 btn-101"
                  style={{ cursor: "pointer" }}
                  onClick={this.handleSubmit}
                  type="submit"
                >
                  Next Step
                </button>
              )}
            </div>
          </div>
        </form>
      </React.Fragment>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  submitDataFromHomePage: data => dispatch(submitDataFromHomePage(data)),
  SubmitValidateData: info => dispatch(SubmitValidateData(info))
});

export default connect(
  undefined,
  mapDispatchToProps
)(withRouter(MiniForm));
