import moment from "moment";
import { fbDatabase, fbAuth } from "../../firebase";
import {
  SUBMIT_COMMON_INFO,
  CONTINUE_WITH_COMMON_INFO,
  PASSWORD_RESET,
  USER_SIGN_IN
} from "./types";

export const submitDataFromHomePage = info => {
  return dispatch => {
    return new Promise((resolve, reject) => {
      fbDatabase
        .ref("users")
        .orderByChild("email")
        .equalTo(info.email)
        .on("value", snap => {
          if (!snap.val()) {
            resolve({
              message: "unused email that we can use",
              status: false,
              info: snap.val()
            });
          } else {
            reject({ message: "email exits", status: true });
          }
        });
    });
  };
};

export const SubmitValidateData = info => {
  const { name, email, accountType } = info;
  let newPostKey = fbDatabase.ref(`users`).push({ name, email, accountType }).key;
  return {
    type: SUBMIT_COMMON_INFO,
    payload: { ...info, _id: newPostKey }
  };
};

export const continueWithCommonUser = async (info, cb) => {
  const { _id, phone, name, accountType, email, amount, time, password } = info;

  const createdUser = await fbAuth
    .createUserWithEmailAndPassword(email, password)
    .then(() => {
      let currentUser = fbAuth.currentUser;
      currentUser
        .sendEmailVerification()
        .then(() => {
          if (accountType === "borrower") {
            fbDatabase.ref(`borrower`).push({
              amount,
              time,
              createdAt: moment().format(),
              uid: currentUser.uid
            });
          }
          if (!_id) {
            fbDatabase.ref(`users`).push({
              uid: currentUser.uid,
              phone,
              name,
              accountType,
              email
            });
          } else {
            fbDatabase.ref(`users/${_id}`).set({
              uid: currentUser.uid,
              phone,
              name,
              accountType,
              email
            });
          }
          cb({ success: true });
        })
        .catch(err => cb(err));
    })
    .catch(err => {
      return cb(err);
    });

  return {
    type: CONTINUE_WITH_COMMON_INFO,
    payload: createdUser
  };
};

export const signInUser = async (email, password, cb) => {
  const data = await fbAuth
    .signInWithEmailAndPassword(email, password)
    .then(user => {
      return {
        loginStatus: {
          user,
          loginSuccess: true
        }
      };
    })
    .catch(err => {
      return {
        loginStatus: {
          err,
          loginSuccess: false
        }
      };
    });

  return {
    type: USER_SIGN_IN,
    payload: data
  };
};

export const getCurrentUser = loginUser => {
  const currentUser = fbAuth.onAuthStateChanged(loginUser => {
    if (loginUser) {
      return loginUser;
    } else {
      console.log("No user logged in");
    }
  });
  console.log(currentUser);
  return {
    type: "GET_C_U",
    payload: currentUser
  };
};

export const resetPassword = async (email, cb) => {
  await fbAuth
    .sendPasswordResetEmail(email)
    .then(() => {
      cb(null, {
        message: "Password RESET mail is sent. Please check your inbox."
      });
    })
    .catch(err => {
      // console.log(err);
      cb(err, null);
    });

  return {
    type: PASSWORD_RESET
  };
};
